import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class RenovarService {

  constructor(private http: HttpClient) { }
  private urlbase: string = 'https://api.actuar.net/Apuracao/'
  
  renovarAssinaturaPessoa(pessoaid): Observable<any>{
    return this.http.get(`${this.urlbase}RenovarAssinaturasApartirDoCicloApuracao?pessoaId=${pessoaid}`,{observe : 'response'})
  }

  apurarAssinaturaPessoa(pessoaid): Observable<any>{
    return this.http.get(`${this.urlbase}Apurar?pessoaId=${pessoaid}`)
  }

  faturarAssinaturaPessoa(pessoaid): Observable<any>{
    return this.http.get(`${this.urlbase}Faturar?pessoaId=${pessoaid}`)
  }
}
